.btn {
    display: block;
    cursor: pointer;
    padding: 12px;
    @include size(160px);
    background-color: transparent;
    border: 1px solid getColor('accent-color');
    border-radius: 25px;
    text-align: center;
    text-transform: uppercase;

    font-size: getSize('btn');
    font-weight: 600;
    letter-spacing: 1.08px;
    transition: $time $cubic;

    &:hover {
        background-color: getColor('accent-color');
        transition: $time $cubic;
    }

    &.white {
        color: getColor('main-bg-color');
    }

    &.center {
        margin: 0 auto;
    }
}